@import url(https://fonts.googleapis.com/css2?family=Bahiana&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  width: 100vw;
  background-color: silver;
  margin: 0;
  font-family: "Bahiana", cursive;
  /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.marg {
  display: flex;
  flex-direction: row;
  width: 100vw;
}
d {
  color: maroon;
}

.App {
  text-align: center;
}

#about {
  min-height: 90vh;
  padding-top: 15vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  width: 80vw;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  padding-bottom: 5vh;
}
#about h2 {
  font-size: 5rem;
  text-align: center;
  font-family: "Bahiana", cursive;
}

@media screen and (max-width: 1000px) {
  #about {
    padding-top: 5vh;
  }
}

#contact {
  background-color: rgb(100, 100, 100);
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  width: 100vw;
  padding: 20px;
  font-family: sans-serif;
  font-weight: 100;
}
.socials {
  margin-top: 2rem;
  display: inline-flex;
  margin-bottom: 2rem;
}
.socials div {
  margin-left: 2rem;
  margin-right: 2rem;
}
img {
  height: 2rem;
  width: 2rem;
}

.number {
  border: none;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  font-size: 20vw;
}
.name {
  font-size: 5vw;
}
#countdown {
  height: 90vh;
  background-color: aliceblue;
  font-size: 7vw;
  padding-top: 15vh;
  width: 100vw;
}
@media screen and (max-width: 1000px) {
  #countdown {
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    padding-bottom: 10vh;
  }
}

#howto {
  background-color: aliceblue;
  min-height: 90vh;
  padding-top: 15vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  width: 100vw;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
}
#howto h2 {
  font-size: 5rem;
  text-align: center;
  font-family: "Bahiana", cursive;
}
#howto .content {
  margin-left: auto;
  margin-right: auto;
  width: 80vw;
}
li {
  margin-bottom: 1rem;
}

@media screen and (max-width: 1000px) {
  #howto {
    padding-top: 5vh;
  }
}

#prizes {
  padding-top: 15vh;
  display: block;
}
.prize {
  font-size: 3rem;
  font-family: sans-serif;
  display: block;
  width: 100vw;
}
.emo {
  font-size: 10rem;
  margin-left: auto;
  margin-right: 1rem;
}
.cash {
  margin-right: auto;
}
.message {
  font-family: sans-serif;
  margin-top: 2rem;
  font-size: 3rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15vh;
}
.prizes {
  max-width: 100vw;
  display: flex;
  flex-direction: row;
}

h2 {
  font-size: 5rem;
  text-align: center;
  font-family: "Bahiana", cursive;
}

@media screen and (max-width: 1000px) {
  .prizes {
    flex-direction: column;
  }
}

.bar {
  position: fixed;
  top: 0;
  width: 100vw;
  overflow: hidden;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 4px;
  display: flex;
  font-size: 2rem;
  text-decoration: none;
  background-color: rgb(100, 100, 100);
}
.bar a:hover {
  color: sliver;
}
.bar a {
  margin: auto;
  text-decoration: none;
  color: black;
}
.baritem {
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 1000px) {
  .bar {
    font-size: 5vw;
  }
}

